<template>
  <div v-if="list.length > 0" class="swiper-box">
    <div class="btn">
      <img :src="leftImg" @click="left">
    </div>
    <van-swipe ref="swiper" class="my-swipe" :autoplay="3000" indicator-color="white" :show-indicators="false">
      <van-swipe-item v-for="(item, i) in list" :key="i">
        <img :src="item.image_url" alt="">
        <div class="content-text-box">
          <div class="t">{{ item.title }}</div>
          <div class="b">{{ item.created_at }}</div>
        </div>
      </van-swipe-item>
    </van-swipe>
    <div class="btn">
      <img :src="rightImg" @click="right">
    </div>
  </div>
</template>
<script>
import leftImg from '@/assets/index_left.png'
import rightImg from '@/assets/index_right.png'
import { getActiveNewsList } from '@/api'
export default {
  data() {
    return {
      leftImg,
      rightImg,
      list: []
    }
  },
  created() {
    const data = {
      page: 1,
      per_page: 8
    }
    getActiveNewsList(data).then(res => {
      this.list = res.data
    }).catch(() => {})
  },
  methods: {
    left() {
      this.$refs.swiper.prev()
    },
    right() {
      this.$refs.swiper.next()
    }
  }
}
</script>
